import React from 'react'

import Navbar from './Navbar'

const iframeUrl = "https://snapwidget.com/embed/806451"



const CarouselLayout = (props) => {
    /**
     * render first section with navigation and wth iframe
     * iFrame will take post from instagram (through CDN)
     *
     * Renders
     *      1. Navbar
     *
     * Parent
     *      App
     */
    const { windowSize:{windowWidth , windowHeight} } = props

    return (
        <React.Fragment>
            <Navbar isNavTransparent={props.transparentNavbar}/>
            <div style={{width:windowWidth, height:windowHeight, overflow:'hidden',position:'relative'}}>
                <div style={{position:'absolute',backgroundColor:'#000',top:0,bottom:0,left:0,right:0,opacity:0.6}}></div>
                {/* <iframe src={iframeUrl} scrolling="no" allowtransparency="true" class="lightwidget-widget" style={{maxWidth:'100%',width:windowWidth,height:windowHeight,objectFit:'cover',border:'0',overflow:'hidden'}}></iframe> */}
                <iframe src={iframeUrl} scrolling="no" allowtransparency="true" className="snapwidget-widget" frameBorder="0" scrolling="no" style={{maxWidth:'100%',width:windowWidth,height:windowHeight,objectFit:'cover',border:'0',overflow:'hidden'}}></iframe>
            </div>
        </React.Fragment>
    )
}

export default CarouselLayout
