import React from 'react'
import Axios from 'axios'
import Zuck from 'zuck.js'

import Navbar from '../LandingPage/CarouselLayout/Navbar'
import Footer from '../LandingPage/Footer'
import PublicIP from '../Commons/PublicIP'

import './story.css'
import '../Commons/loader.css'
import './app.scss'

import ERROR_IMG from '../assets/error.png'
// helpers
import get from 'lodash.get'
import size from 'lodash.size'


const badgeSize = 200

function formatDate(stringTime) {
    let timeIndex = 0;
    let shifts = [35, 60, 60 * 3, 60 * 60 * 2, 60 * 60 * 25, 60 * 60 * 24 * 4, 60 * 60 * 24 * 10];
    let newDate = null;
    if (stringTime != null && stringTime.length > 0) {
        newDate = new Date(stringTime.split(' ')[0]);
    } else {
        newDate = new Date();
    }
    let shift = shifts[timeIndex++] || 0;
    let date = new Date(newDate - shift * 1000);
    let newTimeStamp = date.getTime() / 1000;
    return newTimeStamp;
}

var images = new Array()
function preload(preloadData, ref, allImagesLoadedCallback) {
  var loadedCounter = 0;
  var toBeLoadedNumber = preloadData.customerProductContentsDb.length;
  for (var i = 0; i < preloadData.customerProductContentsDb.length; i++) {
    var url = preloadData.customerProductContentsDb[i].content;
    preloadImage(url, function(){
      loadedCounter++;
      console.log('Number of loaded images: ' + loadedCounter);
      if(loadedCounter == toBeLoadedNumber){
        allImagesLoadedCallback(ref);
      }
    });
  }
  function preloadImage(url, anImageLoadedCallback){
      var img = new Image();
      img.onload = anImageLoadedCallback;
      img.src = url;
  }
}

class AppWrapper extends React.Component {

    state = {
        storyData: false,
        error: false,
        imagesLoaded: false,
    }


    apiCall = (qrId, ip = false) => {
        let url = `https://u6975rdgmd.execute-api.ap-south-1.amazonaws.com/beta/customer/customerProduct/${qrId}/webAPI`
        if (ip) {
            url = `https://u6975rdgmd.execute-api.ap-south-1.amazonaws.com/beta/customer/customerProduct/${qrId}/webAPI${`?ipAddress=${ip}`}`
        }
        Axios.get(url, {
            headers: {
                'content-type': 'application/json'
            }
        }).then(res => {
            let data = get(res, "data", false)
            console.log('Preloading images');
            preload(data, this, function(ref){
              console.log('All images were preloaded');
              ref.setState({
                  imagesLoaded: true
              })
            })
            this.setState({
                storyData: data,
                error: false
            })
        }).catch(err => {
            this.setState({
                error: true
            })
        })
    }
    componentDidMount = () => {
        let qrId = get(this.props, "qrId", false)
        // get data from api
        if (qrId) {
            PublicIP().then(ip => {
                this.apiCall(qrId, ip)
            }).catch(() => {
                this.apiCall(qrId)
            })
        }
    }

    renderErrorBlock = () => {
        return (
            <React.Fragment>
                <Navbar isNavTransparent={false} />
                <div style={{ overflow: 'hidden' }}>
                    <div className="errImageWrapper" style={{ height: window.innerHeight, width: window.innerWidth }}>
                        <img src={ERROR_IMG} className="errImg" />
                    </div>
                </div>
                <Footer />
            </React.Fragment >
        )
    }

    renderLoader = () => {
        return (
            <div className="frame">
                <div className="center">
                    <div className="dot-1"></div>
                    <div className="dot-2"></div>
                    <div className="dot-3"></div>
                </div>
            </div>
        )
    }

    render = () => {
        // This will remove product code from ur
        window.history.replaceState(null, null, window.location.pathname);
        if (this.state.error) {
            return this.renderErrorBlock()
        }
        if (this.state.storyData && this.state.imagesLoaded) {
            return <App storyData={this.state.storyData} />
        } else {
            return this.renderLoader()
        }
    }
}
class App extends React.Component {

    constructor(props) {
        super(props);

        let storyItemsData = props.storyData.customerProductContentsDb.map((product, index) => {
            let newTimeStamp = null
            return [
                Math.random().toString(36).slice(2), // Story Item Id
                "photo", // Story Item Type
                3, // Story Duration
                product.content, // Story Item Content
                product.content, // Story Item Preview
                "", // Story Item Link
                false, //
                false, // Story Item Seen
                formatDate(product.created) // Time
            ]
        })

        let mainStory = [
            Zuck.buildTimelineItem(
                "mainStory", // Story Id
                props.storyData.customerProductImageUrl, // Story Image Url
                "", // Story Name
                "", // Story Url
                formatDate(), // Story Timestamp
                storyItemsData // Story Image Items
            )
        ]

        this.storiesElement = null;
        this.storiesApi = null;

        this.state = {
            stories: mainStory,
            productData: JSON.parse(get(props.storyData, "productData", {}))
        }
    }


    componentDidMount() {
        let currentSkinParam = {
            avatars: true,
            list: false,
            autoFullScreen: false,
            cubeEffect: true,
            paginationArrows: false
        }
        let currentSkin = {
            name: 'Snapgram',
            params: currentSkinParam
        }
        this.storiesApi = new Zuck(this.storiesElement, {
            backNative: true,
            previousTap: true,
            skin: currentSkin['name'],
            autoFullScreen: currentSkin['params']['autoFullScreen'],
            avatars: currentSkin['params']['avatars'],
            paginationArrows: currentSkin['params']['paginationArrows'],
            list: currentSkin['params']['list'],
            cubeEffect: currentSkin['params']['cubeEffect'],
            localStorage: true,
            stories: this.state.stories,
            reactive: true,
            callbacks: {
                onDataUpdate: function (currentState, callback) {
                    this.setState(state => {
                        state.stories = currentState;
                        return state;
                    }, () => {
                        callback();
                    });
                }.bind(this)
            }
        });
    }


    render() {
        const timelineItems = []
        this.state.stories.forEach((story, storyId) => {
            const storyItems = [];
            story.items.forEach((storyItem) => {
                storyItems.push(
                    <li key={storyItem.id} data-id={storyItem.id} data-time={storyItem.time} className={(storyItem.seen ? 'seen' : '')}>
                        <a href={storyItem.src} data-type={storyItem.type} data-length={storyItem.length} data-link={storyItem.link} data-linkText={storyItem.linkText}>
                            <img src={storyItem.preview} />
                        </a>
                    </li>
                );
            });

            let arrayFunc = story.seen ? 'push' : 'unshift';
            timelineItems[arrayFunc](
                <div className={(story.seen ? 'story seen' : 'story')} key={storyId} data-id={storyId} data-last-updated={story.lastUpdated} data-photo={story.photo} style={{ margin: '0 auto' }}>
                    <a className="item-link" href={story.link} >
                        <span className="item-preview" style={{ width: badgeSize, height: badgeSize, maxHeight: badgeSize }}>
                            <img src={story.photo} />
                        </span>
                        <span className="info" itemProp="author" itemScope="" itemType="http://schema.org/Person">
                            <strong className="name" itemProp="name">{story.name}</strong>
                            <span className="time">{story.lastUpdated}</span>
                        </span>
                    </a>

                    <ul className="items">
                        {storyItems}
                    </ul>
                </div>
            );
        });
        return (
            <div>
                <Navbar isNavTransparent={false} />
                <div style={{ marginTop: badgeSize * (2 / 3), marginLeft: (window.innerWidth / 2) - (badgeSize / 2) }}>
                    <div ref={node => this.storiesElement = node} id="stories-react" className="storiesWrapper">
                        {timelineItems}
                    </div>
                </div>
                {size(this.state.productData) > 0 &&
                    <center>
                        <div
                            className="linkWrapper"
                            onClick={() => {
                                window.open(get(this.state.productData, "actionUrl", ""), "_blank")
                            }}>
                            <div className="textContainer">
                                <div className="title">{get(this.state.productData, "actionUrlTitle", '')}</div>
                                {/* <div className="link">{get(this.state.productData, "actionUrl", '')}</div> */}
                            </div>
                            {/* <div className="iconWrapper">
                                <i className="icon ion-ios-arrow-forward" style={{ fontSize: '2rem', color: '#fff' }}></i>
                            </div> */}
                        </div>
                    </center>
                }
                <div style={{ position: 'absolute', bottom: 0, left: 0, right: 0 }}>
                    <Footer />
                </div>
            </div>
        );
    }
}

export default AppWrapper
