import React , { Children } from 'react'
import PropTypes from 'prop-types'

const Section = (props) => {
    /**
     * this is wrapper component for content in section of page
     * calculates nav bar height and side padding
     * 
     * after block width and height will be passed to each children
     */
    const { windowSize, enableTopPadding } = props
    const { windowWidth, windowHeight } = windowSize
    
    const wrapperStyle = {
        paddingTop: enableTopPadding && (windowWidth < 600 ? 56 : 64),
        paddingBottom: windowWidth < 600 ? 56 : 64,
        marginLeft: windowWidth > 600 ? windowWidth*0.05 : 0, // 5% padding
        marginRight: windowWidth > 600 ? windowWidth*0.05 : 0, // 5% padding
        overflow:'hidden'
    }

    const childprops = {
        parentWidth: windowWidth > 600 ? windowWidth - (windowWidth*0.05 ) : windowWidth, // section block width
        windowWidth,
        windowHeight
    }
    const childerWithProps = Children.map(props.children,(child) => {
        return React.cloneElement(child,{ childprops })
    })
    return (
        <div style={wrapperStyle} className={props.className}>
            {childerWithProps}
        </div>
    )
}

Section.propTypes = {
    className:PropTypes.string,
    windowSize:PropTypes.shape({
        windowWidth:PropTypes.number.isRequired,
        windowHeight:PropTypes.number.isRequired
    }),
    enableTopPadding:PropTypes.bool // for listing extra padding issue
}

Section.defaultProps = {
    className:'',
    enableTopPadding:true
}

export default Section