import React , { useState , useEffect }from 'react';

// modules
import CarouselLayout from './CarouselLayout'
import Features from './Features'
import SharingLayout from './SharingLayout'
import CompanyDetails from './CompanyDetails'
import Footer from './Footer'

import MetaTags from 'react-meta-tags';


function App(props) {


    //=================================================================================================
    // window size listner
    //=================================================================================================


    const [ windowWidth, setWindowWidth ] = useState(window.innerWidth)
    const [ windowHeight, setWindowHeight ] = useState(window.innerHeight)
    useEffect(() => {
        const handleSize = () => {
            setWindowWidth(window.innerWidth)
            setWindowHeight(window.innerHeight)
        }
        window.addEventListener("resize",handleSize)
        return () => {
            window.removeEventListener("resize")
        }
    },[])


    //=================================================================================================
    // window scroll listner
    //=================================================================================================


    const [ transparentNavbar, setTransparentNavbar ] = useState(true)
    const [ showFirstBlock, setShowFirstBlock ] = useState(false) // handle visiblity of first block content
    const [ showSecondBlock, setShowSecondBlock ] = useState(false) // handling visibility of second block content
    const [ showThirdBlock, setShowThirdBlock ] = useState(false) // handler visibility of third block content

    useEffect(()=>{
        const handleScroll = () => {
            let scrollY = window.scrollY

            // manage visiblity for nav
            if(scrollY > 200){
                setTransparentNavbar(false)
            }else{
                setTransparentNavbar(true)
            }

            // manage visiblity of first block
            if(scrollY > 300){
                setShowFirstBlock(true)
            }

            // manage visiblity of second block
            if (scrollY > 600){
                setShowSecondBlock(true)
            }

            // manage visiblity of third block
            if (scrollY > 900) {
                setShowThirdBlock(true)
            }
        }

        window.addEventListener("scroll",handleScroll)

        return () => {
            window.removeEventListener("scroll",handleScroll)
        }
    },[])


    return (
        <div style={{boxSizing:'border-box',overflowX:'hidden'}}>
            <MetaTags>
              <title>Buy Cool, Customized and Scannable Badges Online India</title>
              <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
              <meta http-equiv="Content-Type" content="text/html; charset=ISO-8859-1" />
              <meta name="robots" content="noindex" />
              <meta name="geo.region" CONTENT="IN" />
              <meta property="og:title" content="mercNmore Badges: Do more with Badges!" />
              <meta property="og:type" content="article" />
              <meta property="og:url" content="https://www.mercNmore.com/" />
              <meta property="og:image" content="https://ik.imagekit.io/vw6b6og0xz/IMG_2763.jpg" />
              <meta property="og:description" content="Buy Cool, Customized and Scannable Badges Online India" />
            </MetaTags>
            <CarouselLayout windowSize={{windowWidth,windowHeight}} transparentNavbar={transparentNavbar}/>
            <div style={{maxWidth:1500,margin:'0 auto'}}>
                <Features windowSize={{windowWidth,windowHeight}} blocksVisibility={{showFirstBlock,showSecondBlock,showThirdBlock}}/>
            </div>
            <SharingLayout windowSize={{windowWidth,windowHeight}} />
            <div style={{maxWidth:1500,margin:'0 auto'}}>
                <CompanyDetails windowSize={{windowWidth,windowHeight}} blocksVisibility={{showFirstBlock,showSecondBlock,showThirdBlock}}/>
            </div>
            <Footer />
        </div>
    );
}

export default App;
