import React from "react";
import ReactDOM from "react-dom";
import "./index.scss";
import App from "./LandingPage/App";
import QrApp from "./CustomerProductPage/App";
import ProductDetails from "./ProductDetails/App";
import * as serviceWorker from "./serviceWorker";
// helpers
import split from "lodash.split";

// first seperate with ? and then seperate with =
let payload = split(window.location.href, "?");
if (payload != undefined && payload.length === 2) {
    let params = new URL(window.location.href).searchParams;
    let maybeCode = params.get('c');
    let maybeProduct = params.get('p');
    let maybeDownload = params.get('download');
    let maybeHelp = params.get('help');
    if(maybeCode != null) {
      let qrId = maybeCode;
      ReactDOM.render(
          <QrApp qrId={qrId} />,
          document.getElementById("root")
      );
    } else if(maybeProduct != null) {
      let parentProductId = maybeProduct;
      ReactDOM.render(
          <ProductDetails productId={parentProductId} />,
          document.getElementById("root")
      );
    } else if (maybeDownload != null) {
      // Try to directly redirect to App Store or Play Store
      downloadRedirect();
      // If Redirect fails open LandingPage App
      ReactDOM.render(<App />, document.getElementById("root"));
    } else if (maybeHelp != null) {
      // Open LandingPage App and scroll to bottom (Contact us section)
      ReactDOM.render(<App />, document.getElementById("root"));
      scrollToBottom();
    } else {
      ReactDOM.render(<App />, document.getElementById("root"));
    }
} else {
    ReactDOM.render(<App />, document.getElementById("root"));
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

// helpers
function downloadRedirect() {
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (/android/i.test(userAgent)) {
        // Android
        window.location.href =
            "https://play.google.com/store/apps/details?id=com.mercnmore";
    } else if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
        // iOS
        window.location.href =
            "https://apps.apple.com/in/app/mercnmore/id1509395519";
    }
}

function scrollToBottom() {
  window.scrollTo({
    top: document.body.scrollHeight,
    left: 0,
    behavior: 'smooth'
  });
}
